<template>
  <div class="text-left">
        <div id="editor"></div>
  </div>
</template>
<script>
import Quill from 'quill'
export default {
  created () {
      this.axios.get('blog/'+this.$route.params.id).then(res=>{
          var quill = new Quill('#editor', {
             theme: 'bubble',
            "modules": {
      "toolbar": false
  },
              readOnly: true,
        });
        quill.setContents(JSON.parse(res.data.html))
       
        
      })
  },
}
</script>


